.default-input-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    color: #245501;
    padding-bottom: 2vh;
}

.default-label {
    font-size: 1.5rem;
}

.default-input {
    width: 90%;
    font-size: 2rem;
    border-bottom: 2px solid #538D22;
    background-color: transparent;
}

@media (max-width: 768px) {    
    .default-input-container {
        margin-bottom: 30px;
    }

    .default-label {
        font-size: 1.3rem;
    }
    
    .default-input {
        width: 100%;
        font-size: 1.5rem;
        border-bottom: 2px solid #538D22;
        background-color: transparent;
    }
}
