.insert-product-page {
    min-height: 74vh;
    display: flex;
    flex-direction: column;
}

.data-containers {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-left: 50px;
    padding-right: 50px;
}

.insert-product-data {
    width: fit-content;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.insert-image-data {
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 6vh;
}

.drop-box {
    display: flex;
    gap: 1vw;
    padding-bottom: 3vh;
}

.drop-box select {
    width: fit-content;
    font-size: 1.5rem;
    border: solid 1px #538D22;
    border-radius: 10px;
    padding: 5px;
}

.product-input-title {
    color: #245501;
    font-weight: 400;
    font-size: 2rem;
    padding-bottom: 1vh;
}

.product-input {
    font-size: 1.5rem;
    border: solid 1px #538D22;
    border-radius: 10px;
    padding: 5px;
}

.input-container {
    padding-bottom: 3vh;
}

.input-container textarea {
    font-size: 1.5rem;
    border: solid 1px #538D22;
    border-radius: 10px;
    padding: 5px;
    resize: none;
}

.promotion {
    display: flex;
    flex-direction: column;
}

.price-values {
    display: flex;
    flex-direction: column;
}

#options-weight {
    display: flex;
    flex-direction: column;
}

.options-weight {
    display: flex;
    gap: 1vw
}

.product-weight {
    animation: fadeIn 0.3s ease-in-out;
    display: flex;
    gap: 0.5vw;
}

.product-price {
    width: fit-content;
    display: flex;
    align-items: center;
    gap: 0.5vw;
}

.product-price-coin {
    color: #245501;
    font-weight: 400;
    font-size: 1.5rem;
}

.register-button {
    display: flex;
    justify-content: center;
}

@media (max-width: 950px) {
    .data-containers {
        flex-direction: column;
    }

    .product-input-title {
        font-size: 1rem;
    }

    .drop-box {
        gap: 3vw;
    }

    .drop-box select {
        font-size: 0.7rem;
    }

    .product-input {
        font-size: 1rem;
    }

    .input-container textarea {
        font-size: 1rem;
        border: solid 1px #538D22;
        border-radius: 10px;
        padding: 5px;
        resize: none;
    }

    .product-price {
        gap: 3vw;
    }

    .product-price-coin {
        font-size: 1rem;
    }

    .insert-image-data {
        padding-top: 3vh;
        gap: 3vh;
    }

    .product-quantity {
        width: 84%;
    }
}
